import React, { Component } from 'react';
import Table from './Table';
import Form from './Form';


import {
    Route,
    NavLink,
    Router
  } from "react-router-dom";
  import Home from "./Home";
  import Stuff from "./Stuff";
  import Contact from "./Contact";

  import PrivateRoute from './_components/PrivateRoute'
  import LoginPage from './LoginPage'
  import { history } from './_helpers/history'
  import HomePage from './HomePage'
  import authenticationService from './_services/authenticationService'
//   import DTappls from './_components/DTappls'
//   import Monitor from './Monitor'


class App extends Component {
    constructor(props) {
        super(props);

        this.state = {
            currentUser: null
        };
    }

    componentDidMount() {
        console.log('App.js: showing the message in componentDiDMount'); 
        authenticationService.currentUser.subscribe(x => this.setState({ currentUser: x }));
        console.log('App.js: showing the message in componentDiDMount after call authenticationService'); 
    }

    logout() {
        console.log('App.js: logout clicked!'); 
        authenticationService.logout();
        history.push('/login');
    }


  render() {
    const { currentUser } = this.state;
    return (
   <Router history={history}>
        <div>
          <h1>Simple SPA</h1>

        {currentUser &&
          <ul className="header">
            <li><NavLink exact to="/">UserInfo</NavLink></li>
            <li><NavLink to="/home">Home</NavLink></li>
            <li><NavLink to="/stuff">Stuff</NavLink></li>
            <li><NavLink to="/contact">Contact</NavLink></li>
            <li><NavLink to="/dtapp">DesignTime</NavLink></li>
            <li><NavLink to="/monitor">Monitor</NavLink></li>
            <li><a onClick={this.logout} className="nav-item nav-link">Logout</a></li>
          </ul>
         }
          <div className="content">           
            {/* <PrivateRoute exact path="/" component={HomePage} />
            <PrivateRoute  exact  path="/home" component={Home}/>
            <PrivateRoute path="/stuff" component={Stuff}/>
            <PrivateRoute path="/contact" component={Contact}/> */}
            {/* <PrivateRoute path="/dtapp" component={DTappls}/>
            <PrivateRoute path="/monitor" component={Monitor} /> */}
            <Route path="/login" component={LoginPage} />
            <h3>This is just a test</h3>
          </div>
        </div>
     </Router>
    );
  }
}

export default App;